import classes from "./Instruction.module.css"
import typo from "../../typo.module.css"
import color from "../../textColor.module.css"
import classNames from "classnames";
import iconOpen from "../../Icons/instructionOpen.png"
import iconAllow from "../../Icons/instructionAllow.png"
import iconInstall from "../../Icons/instructionInstall.png"

function Instruction() {
    return <div className={classes.instruction}>
        <section className={classes.content}>
            <h2 style={{textAlign: "center"}} className={classNames(typo.displayMdRegular, color.primaryInverse)}>How to
                Get</h2>
            <div className={classes.cardWrapper}>
                <div className={classes.card}>
                    <h5 className={classNames(classes.cardTitle, typo.lgSemibold, color.accent)}>Step 1</h5>
                    <div className={classes.image} ><img src={iconOpen} alt="icon"/></div>
                    <div>
                        <p className={classNames(typo.xlSemibold, color.primaryInverse)}>Open</p>
                        <p className={classNames( classes.description, typo.lgMedium, color.secondary)}>Open the KrakenSetup file from the
                            chrome downloads in the upper right corner of this window</p>
                    </div>
                </div>

                <div className={classes.card}>
                    <h5 className={classNames(classes.cardTitle, typo.lgSemibold, color.accent)}>Step 2</h5>
                    <div className={classes.image} ><img src={iconAllow} alt="icon"/></div>
                    <div>
                        <p className={classNames(typo.xlSemibold, color.primaryInverse)}>Allow</p>
                        <p className={classNames( classes.description, typo.lgMedium, color.secondary)}>if prompted, click . Install anyway,
                            and then - Yes in the system windows.
                        </p>
                    </div>
                </div>


                <div className={classes.card}>
                    <h5 className={classNames(classes.cardTitle, typo.lgSemibold, color.accent)}>Step 3</h5>
                    <div className={classes.image} ><img src={iconInstall} alt="icon"/></div>
                    <div>
                        <p className={classNames(typo.xlSemibold, color.primaryInverse)}>Install</p>
                        <p className={classNames( classes.description, typo.lgMedium, color.secondary)}>KrakenDekstop will start
                            automatically</p>
                    </div>
                </div>
            </div>
        </section>
    </div>
}

export default Instruction